body{
    background-color: black;
}
.outer-box{
    width : 500px;
    margin : 2% auto;
}
.box {
    background-color: darkgreen;
    color : white;
    width: 100%;
    height : 170px;
}
.text-end{
    text-align: center;
}
h2, h4{
    margin: 0;
    padding: 10px 10px 10px 0px;
}
.text-addTodo{
    text-align: center;
    margin-top: 10px;
}
input[type = text]
{
    padding: 7px;
    border-radius: 5px;
    width: 250px;
    border:none;
}
.btn-addTodo {
    background-color: dodgerblue;
    font-size: 14px;
    font-weight: 600;
    margin-left: 15px;
    border: none;
    color: white;
    border-radius: 7px;
    cursor: pointer;
    padding: 7px;
}
input[type = text]:focus{
    outline: none;
}

.btn-addTodo:focus{
    outline: none;
}
.box1
{max-height : "210px" ;
 overflow-y : "auto";
}
ul {
    margin: 0 ;
    padding: 0px;
     list-style: "none"; 
}
li{
    display: flex;
    justify-content: space-between;
    background-color: black;
    font-size: 18px;
    font-weight: 600;
    color: yellowgreen;
    padding: 15px 15px 15px 10px;
    border-bottom: 1px solid white ;
    border-top: 1px solid white ;
}
.icon{
display: flex;
justify-content: space-between;
width: 60px;
}
.pointer{
    cursor: pointer;
    color: red;
}
.line-through{
    text-decoration: line-through;
    color: green;
}
.red{
    color: red;
}
.blue {
    color: dodgerblue;
}
.green{
    color: green;
}